import React, { useState, useEffect } from 'react';
import initJournalTracking from './initJournalTracking';
import JournalGroup from './JournalGroup';
import getKey from './KeyGenerator';

const TrackJournal = ({
    journals,
    setJournals,
    isCollapsed,
    //setScroll,
    targetElementRef,
    scrollToBottom
}) => {

    console.log(`journals: ${JSON.stringify(journals, null, 2)}`)
    console.log(`journals2: ${localStorage.getItem('journalTracking')}`);

    useEffect(() => {
        if ((journals === null) && (localStorage.getItem('journalTracking') === null)) {
            setJournals(initJournalTracking);
        }
        localStorage.setItem('journalTracking', JSON.stringify(journals));
    }, [journals]);

    useEffect(() => {
        console.log(`journals1: ${JSON.stringify(journals, null, 2)}`)
        console.log(`journals2: ${localStorage.getItem('journalTracking')}`)
        const storedJournals = (journals !== null) ? journals : JSON.parse(localStorage.getItem('journalTracking'));
        console.log(`storedJournals: ${JSON.stringify(storedJournals, null, 2)}`)
        if (storedJournals) {
            setJournals(storedJournals);
        } else {
            setJournals(initJournalTracking);
        }
    }, []);

    const handleDeleteTask = (journalGroupIndex, journalIndex) => {
        const updatedJournals = [...journals];
        updatedJournals[journalGroupIndex].journals.splice(journalIndex, 1);
        setJournals(updatedJournals);
    };

    const notNull = (value) => (value !== null) ? true : false;
    const notEmpty = (value) => (value !== "") ? true : false;
    const isGood = (value) => (notNull(value) && notEmpty(value)) ? true : false;

    const addJournal = (journalGroupIndex, journalIndex) => {
        const updatedJournals = [...journals];
        const journalDescription = prompt('Journal description:', '');
        //const journal = prompt('Journal:', '');
        const newJournal = {
            description: journalDescription,
            //journal: journal
            journal: ''
        }
        if (isGood(journalDescription)/* || isGood(journal)*/) {
            updatedJournals[journalGroupIndex].journals.push(newJournal)
            setJournals(updatedJournals);
        }

    };
    const subtractJournal = (journalGroupIndex, journalIndex) => {
        const updatedJournals = [...journals];
        const journal = updatedJournals[journalGroupIndex].journals[journalIndex];
        journal.sessions = journal.sessions ?? [];
        journal.isRunning = false;
        journal.sessions.pop();
        setJournals(updatedJournals);
    };

    const deleteGroup = (journalGroupIndex) => {
        const toggle = window.confirm(`Are you sure you want to remove journal group ${journals[journalGroupIndex].title}`)
        const removeItemByIndex = (array, index) => {
            if (index >= 0 && index < array.length) {
                array.splice(index, 1);
            } else {
                console.error("Index out of range");
            }
        };
        if (toggle) {
            const newJournals = [...journals];
            removeItemByIndex(newJournals, journalGroupIndex);
            setJournals(newJournals);
        }
    }

    return (
        <div key={getKey('journalGroupContainer')}>
            {journals.map((journalGroup, journalGroupIndex) => (
                <div key={getKey('journalGroups')}>
                    <JournalGroup
                        isCollapsed={isCollapsed}
                        journals={journals}
                        setJournals={setJournals}
                        journalGroup={journalGroup}
                        journalGroupIndex={journalGroupIndex}
                        deleteGroup={deleteGroup}
                        addJournal={addJournal}
                        //setScroll={setScroll}
                        targetElementRef={targetElementRef}
                        scrollToBottom={scrollToBottom}
                    />
                </div>
            ))}
        </div>
    )
}

export default TrackJournal