import React, { useState, useEffect } from 'react';
import getKey from '../utils/KeyGenerator';
import TimerDisplay from '../utils/TimerDisplay';
import { currentTime, currentDate } from '../utils/CurrentCalendar';
import initSession from './initSession';
import initTasks from './initTasks';
import initTask from './initTask';
import icons from './icons';
import getTotalTime from './getTotalTime';
import Task from './Task';

const TrackTasks = ({
    tracking,
    tasks,
    setTasks,
    getProjectTime,
    deleteProject,
    toggleCollapseSubmenu,
    isCollapsed,
    toggleParentTimer
}) => {

    const [collapsedProjects, setCollapsedProjects] = useState([]);
    const getTaskTotalTime = (task) => task.sessions.reduce((total, session) => total + session.runningTime, 0);
    const getLocalProjectTotalTime = (projectIndex) => {
        const localTasks = JSON.parse(localStorage.getItem('taskTracking'));
        const totalTime = ((localTasks != []) && localTasks[projectIndex]) ? localTasks[projectIndex].totalTime : '00:00:00';
        //alert(`totalTime: ${totalTime}`)
        const displayTime = (String(totalTime).indexOf(':') > -1) ? totalTime : (totalTime === 0) ? '00:00:00' : getTotalTime(totalTime);
        //alert(`displayTime: ${displayTime}`)
        return displayTime;
        //tasks[projectIndex].tasks.reduce((total, task) => total + task.totalTime, 0);
    }
    const getProjectTotalTime = (project) => {
        return project.tasks.reduce((total, task) => total + task.totalTime, 0);
    }

    useEffect(() => {
        const newTasks = [...tasks];
        newTasks.map((project, projectIndex) => {
            project.totalTime = getProjectTotalTime(project);
        })
        let dataToString = JSON.stringify(newTasks);
        if (tracking === 'projects') {
            localStorage.setItem('projects', dataToString);
        } else {
            localStorage.setItem('taskTracking', dataToString);
        }
    }, [tasks]);

    useEffect(() => {
        //tasks.map((task, index) => console.log(`TrackTasks => task.description(${index}): ${task.description}`) )
        /*
         const storedTasks = (tracking === 'projects') ? localStorage.getItem('projects') : localStorage.getItem('taskTracking');
         if (storedTasks) {
             setTasks(JSON.parse(storedTasks));
         } else {
             setTasks([initSession(currentDate(), currentTime(), currentDate(), currentTime(), 0)]);
         }
         */
    }, []);
        
    const addTask = (index) => {
        const taskDescription = prompt('Enter task description:');
        if (taskDescription) {
            const sessions = [];
            sessions.push(initSession(currentDate(), currentTime(), currentDate(), currentTime(), 0));
            const task = initTask(taskDescription, sessions);
            const updatedTasks = [...tasks];
            updatedTasks[index].tasks.unshift(task);
            updatedTasks[index].isCollapsed = false;
            updatedTasks.map((project, projectIndex) => {
                project.totalTime = getProjectTotalTime(project);
            })
            setTasks(updatedTasks);
        }
    };
    const handleStartTask = (taskProjectIndex, taskIndex) => {
        const updatedTasks = [...tasks];
        const task = updatedTasks[taskProjectIndex].tasks[taskIndex];
        task.sessions = task.sessions ?? [];
        task.isRunning = true;
        //task.startDate = currentDate();
        task.sessions.push(initSession(currentDate(), currentTime(), currentDate(), currentTime(), 0))
        updatedTasks.map((project, projectIndex) => {
            project.totalTime = getProjectTotalTime(project);
        })
        setTasks(updatedTasks);
        startTimer(taskProjectIndex, taskIndex);
    };
    const handleStopTask = (taskProjectIndex, taskIndex) => {
        const updatedTasks = [...tasks];
        const project = updatedTasks[taskProjectIndex];
        const task = project.tasks[taskIndex];
        task.sessions = task.sessions ?? [];
        task.isRunning = false;
        const startTime = task.sessions[task.sessions.length - 1].startTime;
        const startDate = task.sessions[task.sessions.length - 1].startDate;
        const endTime = currentTime();
        const endDate = currentDate();

        let runningTime = task.sessions[task.sessions.length - 1].runningTime;
        runningTime = (runningTime === undefined)
            ? task.sessions[task.sessions.length - 2].runningTime
            : runningTime;
        //alert(`runningTime: ${runningTime}`)
        const session = initSession(
            startDate,
            startTime,
            endDate,
            endTime,
            runningTime
        )
        //alert(JSON.stringify(session,null,2))
        task.runningTime = runningTime;
        //task.runningTimeDisplay = getTotalTime(runningTime);
        task.sessions.push(session);
        const taskTotalTime = getTaskTotalTime(updatedTasks[taskProjectIndex].tasks[taskIndex]);
        task.runningTimeDisplay = getTotalTime(taskTotalTime);
        task.totalTime = taskTotalTime;
        //const projectTotalTime = getProjectTotalTime(taskProjectIndex);
        //project.totalTime = getTotalTime(projectTotalTime);
        //project.totalTime = projectTotalTime;
        //alert(`${project.description} totalTime: ${project.totalTime}`)
        updatedTasks.map((project, projectIndex) => {
            project.totalTime = getProjectTotalTime(project);
        })
        setTasks(updatedTasks);
        stopTimer(taskProjectIndex, taskIndex);
    };
    
    const startTimer = (taskProjectIndex, taskIndex) => {
        const updatedTasks = [...tasks];
        const task = updatedTasks[taskProjectIndex].tasks[taskIndex];

        task.timerId = setInterval(() => {
            updatedTasks.map((project, projectIndex) => {
                project.totalTime = getProjectTotalTime(project);
            });
            setTasks(updatedTasks);
        }, 1000);
    };

    const stopTimer = (taskProjectIndex, taskIndex) => {
        const updatedTasks = [...tasks];
        clearInterval(updatedTasks[taskProjectIndex].tasks[taskIndex].timerId);
        updatedTasks[taskProjectIndex].tasks[taskIndex].sessions.splice((updatedTasks[taskProjectIndex].tasks[taskIndex].sessions.length - 2), 1);

        const taskTotalTime = getTaskTotalTime(updatedTasks[taskProjectIndex].tasks[taskIndex]);
        updatedTasks[taskProjectIndex].tasks[taskIndex].runningTimeDisplay = getTotalTime(taskTotalTime);
        updatedTasks[taskProjectIndex].tasks[taskIndex].totalTime = taskTotalTime;
        //const projectTotalTime = getProjectTotalTime(taskProjectIndex);
        //updatedTasks[taskProjectIndex].totalTime = getTotalTime(projectTotalTime);
        //updatedTasks[taskProjectIndex].totalTime = projectTotalTime;
        updatedTasks.map((project, projectIndex) => {
            project.totalTime = getProjectTotalTime(project);
        })
        //alert(`stopTimer => ${updatedTasks[taskProjectIndex].description} totalTime: ${updatedTasks[taskProjectIndex].totalTime} taskTotalTime: ${updatedTasks[taskProjectIndex].tasks[taskIndex].totalTime}`)
        setTasks(updatedTasks);
    };

    const handleStartSubTask = (taskProjectIndex, taskIndex) => {
        console.log(`handleStartSubTask => taskProjectIndex: ${taskProjectIndex}, taskIndex: ${taskIndex}`);
        const updatedTasks = [...tasks];
        const task = updatedTasks[taskProjectIndex].tasks[taskIndex];
        task.sessions = task.sessions ?? [];
        task.isRunning = true;
        const subtask = {
            startDate: currentDate(),
            startTime: currentTime(),
            endTime: '',
            endDate: ''
        };
        const session = {
            startDate: currentDate(),
            startTime: currentTime(),
            endTime: ''
        }
        task.sessions.push(subtask)
        updatedTasks[taskProjectIndex].tasks[taskIndex].sessions.push(subtask);
        updatedTasks.map((project, projectIndex) => {
            project.totalTime = getProjectTotalTime(project);
        })
        setTasks(updatedTasks);
        startTimer(taskProjectIndex, taskIndex);
    };
    const handleStopSubTask = (taskProjectIndex, taskIndex) => {
        const updatedTasks = [...tasks];
        const task = updatedTasks[taskProjectIndex].tasks[taskIndex];
        task.sessions = task.sessions ?? [];
        task.isRunning = false;
        const startTime = task.sessions[task.sessions.length - 1].startTime;
        const startDate = task.sessions[task.sessions.length - 1].startDate;
        const endTime = currentTime();
        const endDate = currentDate();
        const totalTimeMilliseconds = Math.round(endTime - startTime);
        const totalTime = getTotalTime(totalTimeMilliseconds);
        let runningTime = task.sessions[task.sessions.length - 1].runningTime;
        runningTime = (runningTime === undefined)
            ? task.sessions[task.sessions.length - 2].runningTime
            : runningTime;
        //alert(`runningTime: ${runningTime}`)
        runningTime = (runningTime === '0s')
            ? totalTimeMilliseconds
            : (runningTime + totalTimeMilliseconds);
        const runningTimeDisplay = getTotalTime(runningTime);
        const session = {
            startDate: startDate,
            startTime: startTime,
            endDate: endDate,
            endTime: endTime,
            totalTime: totalTime,
            runningTime: runningTime,
            runningTimeDisplay: runningTimeDisplay
        }
        //alert(JSON.stringify(session,null,2))
        task.runningTime = runningTime;
        task.runningTimeDisplay = runningTimeDisplay;
        task.sessions.push(session);
        updatedTasks.map((project, projectIndex) => {
            project.totalTime = getProjectTotalTime(project);
        });
        setTasks(updatedTasks);
        stopTimer(taskProjectIndex, taskIndex);
    };
    const handleDeleteTask = (taskProjectIndex, taskIndex) => {
        const updatedTasks = [...tasks];
        const project = updatedTasks[taskProjectIndex];
        project.tasks.splice(taskIndex, 1);
        //updatedTasks[taskProjectIndex].totalTime = project.tasks.reduce((total, task) => total + task.totalTime, 0);
        //alert(`${project.description} totalTime: ${updatedTasks[taskProjectIndex].totalTime}`)
        updatedTasks.map((project, projectIndex) => {
            project.totalTime = getProjectTotalTime(project);
        })
        setTasks(updatedTasks);
    };
    const isProjectCollapsed = (index) => (collapsedProjects.indexOf(index)> -1) ? true : false;
    const collapseProject = (index) => {
        const newCollapsedProjects = [...collapsedProjects];
        const isCollapsed = (newCollapsedProjects.indexOf(index) > -1) ? true : false; 
        const collapsed = () => newCollapsedProjects.indexOf(index);
        if (isCollapsed) {
            newCollapsedProjects.splice(collapsed(),1);
        } else {
            newCollapsedProjects.push(index);
        }
        setCollapsedProjects(newCollapsedProjects);
        console.log(`index: ${index} - isCollapsed: ${isCollapsed} - ${JSON.stringify(collapsedProjects)}`)
    }
    return <div>
        {tasks.map((task, index) => (
            <div key={getKey(`${task.description}${index}`)} className='containerBox'>
                <div className='containerBox bg-lite'>
                    <div className='containerBox flexContainer'>
                        <div className='flex2Column color-yellow bold size25 contentLeft'>
                            {task.description}
                        </div>
                        <div className='flex2Column color-lite contentRight'>
                            {getTotalTime(task.totalTime)}
                        </div>
                    </div>
                    <div className='flexContainer'>
                        <div className='mt-15 ml-15 flex2Column color-lite contentLeft'>
                            <div className=''>{String(task.createdDate).split(', ')[0]}</div>
                        </div>
                        <div className='flex2Column'>
                            <div className='flexContainer'>
                                <div
                                    className='flex3Column containerBox button'
                                    onClick={() => addTask(index)}
                                >
                                    {icons.track}
                                </div>
                                <div
                                    className='flex3Column containerBox button'
                                    onClick={() => deleteProject(index)}
                                >
                                    {icons.delete}
                                </div>
                                <div
                                    className='flex3Column containerBox button'
                                    onClick={() => collapseProject(index)}
                                >
                                    {(task.isCollapsed || isProjectCollapsed(index)) ? icons.down : icons.up }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div>
                    {((isCollapsed ?? task.isCollapsed) || isProjectCollapsed(index))
                        ? null
                        : task.tasks.map((task, taskIndex) => (
                            <Task
                                projects={tasks}
                                setProjects={setTasks}
                                taskIndex={taskIndex}
                                task={task}
                                handleStopTask={handleStopTask}
                                projectIndex={index}
                                handleStartTask={handleStartTask}
                                handleDeleteTask={handleDeleteTask}
                                getTaskTotalTime={getTaskTotalTime}
                                getTotalTime={getTotalTime}
                                getProjectTotalTime={getProjectTotalTime}
                            />
                        ))}
                </div>
            </div>
        ))}
    </div>
}

export default TrackTasks;