import React, { useState } from 'react';
import WaterTemp from './WaterTemp.js';
import AirTemp from './AirTemp.js';
import WindDirection from './WindDirection.js';
import Sunset from './Sunset.js';
import BuoyReport from './BuoyReport.js';
//import BuoyData from './BuoyData.js';
//import LuecadiaRSS from './LeucadiaRSS.js';
import CollapseToggleButton from '../utils/CollapseToggleButton';
import icons from '../utils/icons';
       

const ConditionsDashboard = ({tideDisplay, setWind}) => {

    const [tideCollapse, setTideCollapse] = useState(false);
    const [conditionCollapse, setConditionCollapse] = useState(false);

    return (
        <div className='m-10'>
            <div className='containerBox'>
                <div className='containerBox bold color-yellow bg-lite'>
                    <CollapseToggleButton
                        title={`TIDE ${icons.moon}`}
                        isCollapsed={tideCollapse}
                        setCollapse={setTideCollapse}
                    />
                </div>
                {
                    (tideCollapse) 
                    ? <div></div> 
                    : <div className='containerBox'>{tideDisplay('wide')}</div>
                }
            </div>
            <div className='containerBox'>
                <div className='containerBox bg-lite'>
                    <CollapseToggleButton
                        title={`CURRENT CONDITIONS`}
                        isCollapsed={conditionCollapse}
                        setCollapse={setConditionCollapse}
                    />
                </div>
                {
                    (conditionCollapse) 
                    ? <div></div> 
                    : <div className='flexContainer'>
                        <div className='containerBox flex2Column'>
                            <div className='containerBox bold color-yellow'>
                                WIND {icons.wind}
                            </div>
                            <WindDirection columns='2' setWind={setWind} height='157px'/>
                        </div>
                        <div className='containerBox flex2Column'>
                            <div className='containerBox bold color-yellow'>
                                WATER {icons.water}
                            </div>
                            <WaterTemp/>
                            <div className='containerBox bold color-yellow'>
                                AIR {icons.temperature}
                            </div>
                            <AirTemp/>
                        </div>
                    </div>
                }
            </div>
            <Sunset view='full'/>
            <BuoyReport />
            {/*<BuoyData />*/}
            {/*<LuecadiaRSS/>*/}
        </div>
    )
}

export default ConditionsDashboard;