 import icons from './icons';
 
 const CollapseToggleButton = ({
    title,
    isCollapsed,
    setCollapse,
    align
 }) => {
    const toggle = () => {
        setCollapse(!isCollapsed);
    }
    return <div className={`button ${(align === 'left') ? 'contentLeft' : (align === 'right') ? 'contentRight' : 'contentCenter'}`} onClick={() => toggle()}>
                <div className={`${(align === 'right') ? 'mr-35' : (align === 'left') ? 'ml-5' : ''}`}>{title}</div>
                <div className={'rt-25 ml-5 size25 button contentRight bold mt--20'}>
                    {
                        (!isCollapsed)
                            ? <div>{icons.up}</div>
                            : <div>{icons.down}</div>
                    }
                </div>
            </div>
 }
export default CollapseToggleButton