import React, { useState, useEffect } from 'react';
import getTotalTime from './getTotalTime';
import icons from './icons';
import CollapseToggleButton from './CollapseToggleButton';

const ChargeProject = ({
    chargeProject,
    chargeProjectIndex,
    getTasks,
    isCollapsed,
    handleDeleteCharges,
    addCharge,  
    getProjectTime                         
}) => {
    const [projectCollapse, setProjectCollapse] = useState(true);
    return  <div key={`chargeProject${chargeProjectIndex}`} className='containerBox'>
                <div className='containerBox'>
                    <div className='containerBox size30 mb-10 bold color-neogreen'>
                        <CollapseToggleButton
                            title={chargeProject.description}
                            isCollapsed={projectCollapse}
                            setCollapse={setProjectCollapse}
                        />
                    </div>
                    <div className='containerBox'>
                        <div className='color-lite size25 bold m-5'>
                            {String(chargeProject.createdDate).split(', ')[0]}
                        </div>
                        <div className='color-lite size25 m-5'>
                            {String(chargeProject.createdDate).split(', ')[1]}
                        </div>
                        <div className='color-lite size20 m-10'>
                            {icons.track} Start {getTotalTime(getProjectTime(chargeProject))}
                        </div>
                    </div>
                    <div className='containerBox flexContainer'>
                        <div className='containerBox flex3Column m-10 bg-lite button' onClick={() => addCharge(chargeProjectIndex)}>
                            + {icons.track}
                        </div>
                        <div className='containerBox flex3Column m-10 bg-lite button' onClick={() => handleDeleteCharges(chargeProjectIndex)}>
                            {icons.delete}
                        </div>
                    </div>
                </div>
                <div>
                    {
                        (isCollapsed || chargeProject.isCollapsed || projectCollapse)
                            ? null
                            : getTasks(chargeProject, chargeProjectIndex)
                    }
                </div>
            </div>
}
export default ChargeProject;