import React, { useState, useEffect } from 'react';
import icons from './icons';
import getKey from '../utils/KeyGenerator';
import CollapseToggleButton from './CollapseToggleButton';

const Task = ({

    projects,
    setProjects,
    taskIndex,
    task,
    handleStopTask,
    projectIndex,
    handleStartTask,
    handleDeleteTask,
    getTaskTotalTime,
    getTotalTime,
    getProjectTotalTime

}) => {

    const [taskCollapse, setTaskCollapse] = useState(true);

    const isValidFormat = (inputString) => {
        const regex = /^\d{2}:\d{2}:\d{2}$/
        return regex.test(inputString);
    }

    const toggleEditSessionTime = (sessionIndex) => {
        const newProjects = [...projects];
        const ogTime = projects[projectIndex].tasks[taskIndex].sessions[sessionIndex].totalTime;
        const selectedSession = newProjects[projectIndex].tasks[taskIndex].sessions[sessionIndex];
        selectedSession.totalTime = prompt('Edit time:', ogTime) || ogTime;
        if (String(selectedSession.totalTime) !== ogTime) {
            if (isValidFormat(String(selectedSession.totalTime))) {
                setProjects(newProjects);
            }
        }
    }

    const getDisplayValue = (unit, value) => (Number(value) < 1) ? '' : `${Number(value)} ${(Number(value) < 2) ? unit.slice(0, -1) : unit}`
    const getTaskTotalDisplay = (task) => {
        if (task.runningTimeDisplay && task.runningTimeDisplay !== '') {
            const hours = getDisplayValue('hours', task.runningTimeDisplay.split(':')[0]);
            const minutes = getDisplayValue('minutes', task.runningTimeDisplay.split(':')[1]);
            const seconds = getDisplayValue('seconds', task.runningTimeDisplay.split(':')[2]);
            //return `${hours} ${minutes} ${seconds}`
            return task.runningTimeDisplay;
        }
        return '';
    }
    const handleDeleteSession = (projectIndex, taskIndex, sessionIndex) => {
        const updatedProjects = [...projects];
        const project = updatedProjects[projectIndex];
        const task = project.tasks[taskIndex];
        task.sessions.splice(sessionIndex, 1);
        const taskTotalTime = getTaskTotalTime(updatedProjects[projectIndex].tasks[taskIndex]);
        task.runningTimeDisplay = getTotalTime(taskTotalTime);
        task.totalTime = taskTotalTime;
        //const projectTotalTime = getProjectTotalTime(projectIndex);
        //project.totalTime = getTotalTime(projectTotalTime);
        //project.totalTime = projectTotalTime;
        //alert(`${project.description} totalTime: ${updatedProjects[projectIndex].totalTime}`)
        updatedProjects.map((project, projectIndex) => {
            project.totalTime = getProjectTotalTime(project);
        })
        setProjects(updatedProjects);
    };
    function formatDurationTime(milliseconds) {
        // Ensure the input is a non-negative number
        milliseconds = Math.max(0, milliseconds);

        // Calculate hours, minutes, and seconds
        const hours = Math.floor(milliseconds / (1000 * 60 * 60));
        const minutes = Math.floor((milliseconds % (1000 * 60 * 60)) / (1000 * 60));
        const seconds = Math.floor((milliseconds % (1000 * 60)) / 1000);

        // Format the result as HH:MM:SS
        const formattedTime = `${String(hours).padStart(2, '0')}:${String(minutes).padStart(2, '0')}:${String(seconds).padStart(2, '0')}`;

        return formattedTime;
    }

    const taskSessions = () => <div className='containerBox'>
        {task.sessions.map((session, sessionIndex) => (
            <div key={`session${sessionIndex}`} className='color-lite'>
                <div className='containerBox'>
                    <div className='columnLeftAlign size25 mb-5'>
                        <span className='bold mr-10 color-yellow size25 lh-30'>
                            {sessionIndex + 1}.
                        </span>
                        {String(session.startDate).split(', ')[0]}
                    </div>
                    <div className='ml-30 columnLeftAlign size15'>
                        {String(session.startDate).split(', ')[1]} - {String(session.endDate).split(', ')[1]}
                    </div>
                    <div className='ml-30 columnLeftAlign size20 bold'>
                        {session.totalTime}
                    </div>
                </div>
            </div>
        ))}
    </div>

    const taskNavigation = () => <div className='containerBox flexContainer'>
        {task.isRunning ? (
            <div className='containerBox flex2Column m-5 bg-lite button' onClick={() => handleStopTask(projectIndex, taskIndex)}>
                {icons.stop}
            </div>
        ) : (
            <div className='containerBox flex2Column m-5  bg-lite button' onClick={() => handleStartTask(projectIndex, taskIndex)}>
                {icons.track}
            </div>
        )}
        <div className='containerBox flex2Column m-5  bg-lite button' onClick={() => handleDeleteTask(projectIndex, taskIndex)}>
            {icons.delete}
        </div>
    </div>
    /*
    return <div key={`task${taskIndex}`} className='containerBox lowerBorder'>
        <div className='containerBox'>
            <CollapseToggleButton
                title={task.description}
                isCollapsed={taskCollapse}
                setCollapse={setTaskCollapse}
            />
            <div className='containerBox bold color-lite size25'>
                <div className='mb-20'>{task.sessions.length} Total</div>
                <div className='containerBox bg-veryLite'>{task.runningTimeDisplay}</div>
            </div>
            {taskNavigation()}
        </div>
        {
            (taskCollapse)
            ? null
            : taskSessions()
        }
    </div>
    */

    return <div key={getKey(`${task.description}${taskIndex}`)} className='containerBox lowerBorder'>
        <div className='containerBox topBorderLite'>
            <div className='color-yellow bold size25 lh-25 pt-5 pb-5 contentLeft'>
                {task.description}
            </div>
            <div className='containerDetail ml--5 mr--5 flexContainer bg-lite'>
                <div className='flex2Column color-lite contentLeft'>
                    {task.sessions.length} Session{(task.sessions.length > 1) ? 's' : ''}
                </div>
                <div className='flex2Column color-lite contentRight'>
                    {getTaskTotalDisplay(task)}
                </div>
            </div>
        </div>
        {task.sessions.map((session, sessionIndex) => (
            <div className='color-lite' key={getKey(`${task.description}${sessionIndex}`)}>
                <div>
                    <div key={getKey(`${task.description}${sessionIndex}${String(session.startDate).split(', ')[0]}`)}>
                        <div className='containerBox topBorderMedium bottomBorderMedium'>
                            <div className='flexContainer'>
                                <div className='flex1Auto contentLeft'>
                                    {String(session.startDate).split(', ')[0]}
                                </div>
                                <div className='flex2Column contentRight'>
                                    <span className='button' onClick={() => toggleEditSessionTime(sessionIndex)}>
                                        {formatDurationTime(session.runningTime)}
                                    </span>
                                </div>
                            </div>
                            <div className='flexContainer pt-10'>
                                <div className='flex2Column contentLeft'>
                                    {String(session.startDate).split(', ')[1].replace(' ', '')} - {String(session.endDate).split(', ')[1].replace(' ', '')}
                                </div>
                                <div className='flex1Auto contentRight'>
                                    <span className='button pt-10'
                                            onClick={() => handleDeleteSession(projectIndex, taskIndex, sessionIndex)}
                                    >
                                        {icons.delete}
                                    </span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className='width-100-percent'>
                    {
                        /* 
                        <TimerDisplay
                            totalTime={session.totalTime}
                            toggleParentTimer={toggleParentTimer}
                            projectIndex={projectIndex}
                            taskIndex={taskIndex}
                            handleStopSubTask={handleStopSubTask}
                            handleStartSubTask={handleStartSubTask}
                        />
                        <TaskTimer
                            todo={session}
                            projectIndex={projectIndex}
                            setTodoCurrentTime={setTodoCurrentTime}
                            todos={todos}
                            setTodos={setTodos}
                            time={todo.time}
                            toggleCheckbox={toggleCheckbox}
                            onReset={onReset}
                        />
                            */
                    }
                </div>
            </div>
        ))}
        <div className='containerBox flexContainer bottomBorderLite'>
            {task.isRunning
                ? <div
                    className='containerBox flex2Column bg-lite button m-5'
                    onClick={() => handleStopTask(projectIndex, taskIndex)}
                >
                    {icons.stop} Stop
                </div>
                : <div
                    className='containerBox flex2Column bg-lite button m-5'
                    onClick={() => handleStartTask(projectIndex, taskIndex)}
                >
                    {icons.track}
                </div>
            }
            <div
                className='containerBox flex2Column bg-lite button m-5'
                onClick={() => handleDeleteTask(projectIndex, taskIndex)}
            >
                {icons.delete}
            </div>
        </div>
    </div>
}
export default Task;