import React, { useState, useEffect } from 'react';
import LinkDisplay from './LinkDisplay';
import CollapseToggleButton from '../utils/CollapseToggleButton';
import icons from './icons';

const LinkGroup = ({

    linkGroup,
    linkProjectIndex,
    addLink,
    deleteGroup,
    isCollapsed,
    links,
    setLinks

}) => {

    const [groupCollapse, setGroupCollapse] = useState(true);
    
    return <div key={`link${linkProjectIndex}`} className='containerBox'>
                <div className='containerBox'>
                    <div className='containerBox bold color-neogreen size30'>
                            <CollapseToggleButton
                                title={linkGroup.title}
                                isCollapsed={groupCollapse}
                                setCollapse={setGroupCollapse}
                            />
                    </div>
                    {
                        (groupCollapse)
                        ? <div></div>
                        : <div className='containerBox flexContainer'>
                                <div 
                                    className='containerBox flex2Column m-10 bg-lite button' 
                                    onClick={() => addLink(linkProjectIndex)}
                                >
                                    <div class="flex2Column size20"><span className='text-outline-light'>➕</span> {icons.link}</div>
                                </div>
                                <div 
                                    className='containerBox flex2Column m-10 bg-lite button' 
                                    onClick={() => deleteGroup(linkProjectIndex)}
                                >
                                    <div class="size20 button">🗑️</div>
                                </div>
                            </div>
                    }
                </div>
                <div>
                    {(isCollapsed || linkGroup.isCollapsed || groupCollapse) 
                    ? null 
                    : linkGroup.links.map((link, linkIndex) => (
                        <LinkDisplay 
                            links={links}
                            setLinks={setLinks}
                            linkGroup={linkGroup}
                            linkProjectIndex={linkProjectIndex}
                            link={link}
                            linkIndex={linkIndex}
                        />
                    ))}
                </div>
            </div>
}
export default LinkGroup;